.preview {
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 10px;
    padding: 10px;
    text-align: center;

   

    h2 {
        font-size: rem(14px);
    }

    h3 {
        margin-top: 10px;
    }

    p {
        margin-top: 0px;
    }


    button {
        background-color: unset;
        border: unset;
        margin-bottom: 10px;
        font-size: 16px;

        &:hover {
            background-color: unset;
            color: unset;
        }
    }

}